body {
  margin: 0;
  padding: 0;
}

.search {
  background-color: #708090;
  padding: 20px 0;
}

.search__form {
  width: 500px;
  margin: auto;
  display: flex;
  box-shadow: 4px 8px 16px rgba(0, 0, 0, .3);
}

.search__form > * {
  background: #fff;
  border: 0;
  border-radius: 0;
  outline: 0;
  padding: 0;
  font-size: 1rem;
  line-height: 50px;
}

.search__input {
  flex-basis: 500px;
  padding-left: 10px;
}

.search__btn {
  color: #fff;
  cursor: pointer;
  background-color: #0a0a0a;
  border: 0;
  padding: 10px;
  transition: all .2s;
  display: flex;
}

.search__btn > svg {
  fill: #fff;
  width: 30px;
  height: 30px;
}

.search__btn:hover {
  background-color: #3e3e3e;
}

